$bright-side: #e1ebf3;
$dark-side: radial-gradient(at 50% 30%, #bc2ba2, #8d1d79);
$darkSideAbout: #792376;
$limeHover: #a1b94c;
$colorForFormTitle: #18295b;
$inputBackgroundColor: #f1f5f9;
$inputColor: #a6adb1;
$labelColor: #485157;
$successfullIcon: #abc64a;
$errorIcon: #e8954b;
$pinkColor: #8d1e7a;
$breadcrumbsBackground: rgba(255, 255, 255, 0.5);
$breadcrumbsHoverColor: #1b2c5e;
$pageArrowShift: 87px;
$headerTextDefaultColor: #fff;
$aboutTextMargin: 2.1875rem;
$darkLime: #708428;
$underlineColor: #944f91;
$underlineColorTransparent: transparent;

@mixin initial-flex {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: flex-start;
	align-content: flex-start;
	align-items: flex-start; }

@mixin absolute-wrapper {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0; }

/* Шрифты */
@mixin font-face($family, $path, $weight: normal, $style: normal) {
  @font-face {
    font-family: $family;
    src: url('#{$path}.woff2') format('woff2'), url('#{$path}.woff') format('woff');
    font-weight: $weight;
    font-style: $style; } }

//Regular
@include font-face('neosanspro', '../fonts/neosanspro-regular', $weight: 400);
//Medium
@include font-face('neosanspro', '../fonts/neosanspro-medium', $weight: 500);
//Light
@include font-face('neosanspro', '../fonts/neosanspro-light', $weight: 100);
//Italic
@include font-face('neosansproitalic', '../fonts/neosanspro-italic', $weight: 400, $style: italic);
//Light Italic
@include font-face('neosansproitalic', '../fonts/neosanspro-lightitalic', $weight: 100, $style: italic);
// Bold
@include font-face('neosanspro', '../fonts/neosanspro-bold', $weight: 700);
// Bold italic
@include font-face('neosansproitalic', '../fonts/neosanspro-bolditalic', $weight: 700, $style: italic);
// Ultra
@include font-face('neosansproultra', '../fonts/neosanspro-ultra', $weight: 800);


body, html {
	$templateWidth: 1360px !global;
	$borderHint: 1px solid #f00 !global;
	$defaultLinkColor:  #485157 !global;
	$defaultGreen: #abc64a !global;
	$greyColor: #b1b1b1 !global;
	$pinkHover: #873984 !global;
	$actionLinkUnderlineColor: #fad33a !global;
	$actionLinkColor: #1b2c5e !global;
	$neoSansPro: 'neosanspro', sans-serif !global;
	$neoSansProItalic: 'neosansproitalic', sans-serif !global;
	$neoSansProUltra: 'neosansproultra', sans-serif !global;
	font-family: $neoSansPro;
	font-weight: 400;
	font-size: 16px;
	color: $defaultLinkColor;
	background-color: #fff; }


.layout-position {
