.page-slider {
	width: calc(100% - 190px);
	padding: 24px 35px 0 35px;
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	justify-content: flex-start;
 }	//height: 80%

.page-slider-navigation {
	text-align: center;
	width: 100%;
	flex-grow: 0;
	flex-shrink: 0;
	padding: 33px 0;
	position: relative;
	.slick-arrow {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		cursor: pointer;
		svg {
			.main-arrow-prev,
			.main-slider-next {
				transition-property: fill;
				transition-duration: 300ms;
				transition-timing-function: ease-in-out; } }
		&:hover {
			svg {
				.main-arrow-prev,
				.main-slider-next {
					fill: #fff; } } } }
	.slick-prev {
	 	left: $pageArrowShift; }
	.slick-next {
		right: $pageArrowShift; } }


.page-slider-image {
	//height: 90%
	padding-top: 0;
	overflow: hidden; }

.page-slider__item {
	img {
 } }		//width: 100%

.page-slider__enumerator {
