.image-dude {
	width: 785px;
	height: 710px;
	position: relative;
	right: 0;
	img {
		position: absolute;
		right: 170px;
		bottom: 80px; }

	&::before {
		content: '';
		width: 512px;
		height: 512px;
		background-color: #fff;
		border-radius: 250px;
		position: absolute;
		bottom: 100px;
		right: 171px;
		z-index: -1; } }
.callback-link__area {
	width: 100%;
	position: absolute;
	bottom: 20px;
	left: 0;
	right: 0;
	text-align: center;
	padding-left: 110px; }
.callback__link {
	color:  $headerTextDefaultColor;
	font-weight: 400;
	vertical-align: top;
	font-size: 14px;
	line-height: 1.06;
	&:hover {
		span::after {
			opacity: 0; } }
	& span {
		height: 25px;
		display: inline-block; }
	& span::after {
		content: "";
		display: block;
		width: 100%;
		height: 2px;
		background-image: -moz-linear-gradient(left, $underlineColor, $underlineColor 60%, transparent 60%);
		background-image: -ms-linear-gradient(left, $underlineColor, $underlineColor 60%, transparent 60%);
		background-image: -o-linear-gradient(left, $underlineColor, $underlineColor 60%, transparent 60%);
		background-image: -webkit-gradient(linear, 0 0, 100% 0, from($underlineColor), color-stop(0.6, $underlineColor), color-stop(0.6, transparent));
		background-image: -webkit-linear-gradient(left, $underlineColor, $underlineColor 60%, transparent 60%);
		background-image: linear-gradient(left, $underlineColor, $underlineColor 60%, transparent 60%);
		background-size: 10px 1px;
		background-repeat: repeat-x;
		background-position: 0 90%;
		margin-top: 10px;
		opacity: 1;
		transition-property: opacity;
		transition-timing-function: ease-in;
		transition-duration: 300ms; }
	&::before {
		content: '';
		display: inline-block;
		vertical-align: top;
		width: 14px;
		height: 25px;
		margin-right: 15px;
		background: url('../images/icons/svg/tel-callback.svg') center center no-repeat; } }

.content-text__wrapper {
	padding: 0 35px;
	width: 100%;
	height: 100%;
	position: relative;
	p {
		width: 100%;
		display: block;
		color: $headerTextDefaultColor;
		font-size: 1rem;
		line-height: 1.6;
		letter-spacing: 0.45px;
		margin-bottom: $aboutTextMargin; }
	blockquote {
		background-color: $headerTextDefaultColor;
		width: 100%;
		margin: 0;
		display: block;
		padding: 25px 60px;
		font-weight: 300;
		font-style: italic;
		font-size: 20px;
		letter-spacing: 0.69px;
		line-height: 1.5;
		position: relative;
		margin-bottom: $aboutTextMargin;
		&::before {
			position: absolute;
			width: 50px;
			height: 50px;
			top: -30px;
			right: 60px;
